import { Block } from '@ali/4ever-cangjie';
import {
  JsonML,
  getTagName,
  getAttributes,
} from '@ali/4ever-utils';
import OnlineVideo from '../models';

export default {
  name: 'onlineVideo',

  match(node: JsonML): boolean {
    return getTagName(node) === 'onlineVideo';
  },

  convert(node: JsonML): Block {
    return OnlineVideo.create({
      data: getAttributes(node) || {},
    });
  },
};
